.scrinshots{
    @include section();
    background-color: $white;

    &__title{
        @include h2();
    }

    &__items{
        display: flex;
        flex-direction: column;

        .screenshot{
            margin-bottom: 2.5rem;

            &:last-child{
                margin-bottom: 0;
            }
        

        @media ($deckstop) {
            flex-basis: calc(50% - 0.8rem);
            margin-bottom: 0;

            &:nth-child(n+3){
                margin-top: 3rem;
            }
        }
    }

        @media ($deckstop) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
        }
    }
}